@mixin btn {
  width: 150px;
  height: 50px;
  text-align: center;
  color: white;
  border-radius: 3px;
  background-color: #f7b446;
  display: block;
  font-size: 17px;
  cursor: pointer;
  padding: 14px 0;
  font-weight: 300;
  border: 0;
  outline: none !important; }

$color_orange: #f7b446;
$color_black: #39374d;

.title {
  font-size: 39px;
  font-weight: 500; }
.title_orange {
  color: $color_orange; }
.title_black {
  color: $color_black; }
.subtitle {
  color: #5e5d6c;
  font-size: 18px;
  font-weight: 400;
  line-height: 28px; }

.btn_white {
  @include btn;
  background-color: white;
  color: black; }

.btn_orange {
  @include btn;
  background-color: $color_orange;
  color: white; }


.squares {
  margin: 10px 0 39px;
  display: flex;
  &_item {
    width: 10px;
    height: 10px;
    margin-right: 3px;
    background-color: #f7b446; } }
* {
  font-family: 'Roboto', sans-serif; }
.btn_orange {
  @include btn; }

.header {
  position: absolute;
  width: 100%;
  &_top {
    a:hover {
      color: #cbcbcb; } }
  &_socials {
    display: flex;
    &_link {
      color: white;
      border-left: 1px solid #425473;
      i {
        padding: 14px 22px; } }
    &_link:last-child {
      border-right: 1px solid #425473; } }
  &_line {
    background-color: #425473;
    width: 100%;
    height: 1px; }
  &_info {
    display: flex;
    height: 100%;
    &_link {
      color: white;
      border-left: 1px solid #425473;
      span {
        display: block;
        padding: 11px 22px;
        font-size: 13px;
        font-weight: 400; } }
    &_link:last-child {
      color: #f7b446; }
    &_link:last-child:hover {
      color: #e29b28 !important; } }
  &_main {
    padding: 19px 0;
    .header_logo {
      &_img {
        margin-right: 10px; } } }
  nav {
    position: relative;
    height: 100%;
