  padding-top: 100px;
  background-color: #f7f9fa;
  position: relative;
  &_item {
    border-radius: 5px;
    border: 1px solid #e7e7e7;
    background: white;
    cursor: pointer;
    transition: .2s all;
    position: relative;
    z-index: 1;
    overflow: hidden;
    &_wrap {
      padding: 39px 54px 45px 34px; }
    img {
      filter: brightness(60%) contrast(110%);
