      display: none;
      width: 100%;
      height: 100%;
      z-index: -1;
      transition: .2s all; }
    &:hover .team_item_bg {
      display: block; }


    &_title {
      font-size: 28px;
      font-weight: 500;
      color: #39374d;
      transition: .5s all; }
    &_subtitle {
      color: #5e5d6c;
